import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'artistname'
})
export class ArtistnamePipe implements PipeTransform {

  transform(value: any) :string {
    let sArtistName : string = "";

    if(value){
      sArtistName = value.name;
    }

    return sArtistName;
  }

}



