import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {SpotifyService} from '../../services/spotify.service';

@Component({
  selector: 'app-artista',
  templateUrl: './artista.component.html'
})
export class ArtistaComponent implements OnInit {

  constructor(private activatedRoute : ActivatedRoute, private _spotiService : SpotifyService) {
  }


  private artist: any;
  private topTracks: any[];

  ngOnInit() {

    this.activatedRoute.params
    .map( (params) => params["id"] )
    .subscribe( (id) => {
      this._spotiService.getArtista(id).subscribe( (dataArtist) => {this.artist = dataArtist; console.log(dataArtist);});
      this._spotiService.getTopTracks(id).subscribe( (data) => {this.topTracks = data; });
    });
  }

}



