import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sinfoto'
})
export class SinfotoPipe implements PipeTransform {

  transform(value: any[]): string {

    let sImage : string = "assets/img/noimage.png";

    if( value && value.length >= 1 ){
      sImage = value[1].url;
    }

    return sImage;
  }

}



