import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import  'rxjs/add/operator/map';

@Injectable()
export class SpotifyService {

  constructor(private http : Http) { }

  private artistas : any[] = [];
  private urlSearch : string = "https://api.spotify.com/v1/search";
  private urlArtista : string ="https://api.spotify.com/v1/artists/";

  //https://api.spotify.com/v1/artists/{id}/top-tracks

  getArtistas(psFilter : string) {
    let sQuery = this.urlSearch +  `?q=${psFilter}&type=artist`;

    return this.http.get(sQuery)
    .map( (res) =>{
      this.artistas = res.json().artists.items;
    } );
  }


  getArtista(psId : string) {
    let sQuery = this.urlArtista +  psId;
    return this.http.get(sQuery)
    .map( (res) =>{
      return res.json();
    } );
  }

  getTopTracks(psId : string) {
    let sQuery = this.urlArtista +  psId + "/top-tracks?country=US";
    return this.http.get(sQuery)
    .map( (res) =>{
      return res.json().tracks;
    } );
  }



}



