import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fotoalbum'
})
export class FotoalbumPipe implements PipeTransform {

  transform(value: any[], pnIndex:number = 0) :string {
    let sImage : string = "assets/img/noimage.png";

    if( value && value.length >= pnIndex ){
      sImage = value[pnIndex].url;
    }

    return sImage;
  }

}



